@import url(https://fonts.googleapis.com/css?family=Lato:400,400i,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i,700,800i&display=swap);
html,body{height:100%}#root{height:100%}body{margin:0;font-family:'Open Sans', Lato, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#e0e5ec}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{display:flex;flex-direction:column;justify-content:center;align-items:center;overflow-y:scroll;padding:3em;margin:auto}@media (min-width: 576px){.App{padding:5em}}.border-container{width:100%;height:100%;position:relative;padding:0 1em 4em 1em;color:#fff;background:#e0e5ec;background-clip:padding-box;border:solid 5px transparent;border-radius:1em}@media (min-width: 576px){.border-container{padding:0 2em 4em 2em}}.border-container:before{content:'';position:absolute;top:0;right:0;bottom:0;left:0;z-index:-1;margin:-5px;border-radius:inherit;background:linear-gradient(145deg, #f0f5fd, #caced4)}#wave{font-size:36px;pointer-events:none}@media (prefers-reduced-motion: no-preference){#wave{-webkit-animation:hand-wave 2 0.5s ease-in-out;animation:hand-wave 2 0.5s ease-in-out}}@-webkit-keyframes hand-wave{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(60deg);transform:rotate(60deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}@keyframes hand-wave{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}50%{-webkit-transform:rotate(60deg);transform:rotate(60deg)}100%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}}

#todo-container{width:100%}#todo-container h1{margin:25px 0;font-size:64px;font-weight:800;text-align:center;color:#e0e5ec;text-shadow:3px 3px 6px rgba(163,177,198,0.6),-3px -3px 6px rgba(255,255,255,0.5);pointer-events:none}@media (min-width: 576px){#todo-container h1{font-size:96px}}.toolbar{display:flex;flex-direction:row;justify-content:center;align-items:center;margin:5px 0 20px 0}.todo-list-container{max-height:500px;overflow-y:scroll;width:100%}.todo-list{display:flex;flex-direction:column}

.todo-item{display:flex;flex-direction:row;align-items:center;margin:10px 15px;padding:5px 10px;color:#6a6d70;cursor:pointer;border-radius:15px;min-height:50px;background-color:#e0e5ec;box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5)}.todo-radio-button{height:20px;width:20px;margin-right:20px;border:none;border-radius:15px;background:linear-gradient(145deg, #f0f5fd, #caced4);box-shadow:2px 2px 4px #bec3c9, -2px -2px 4px #ffffff;cursor:pointer;outline:none}.todo-radio-button:active{background:linear-gradient(145deg, #caced4, #f0f5fd);box-shadow:2px 2px 4px #bec3c9, -2px -2px 4px #ffffff}.todo-radio-button.done{background:linear-gradient(145deg, #caced4, #f0f5fd);box-shadow:2px 2px 4px #bec3c9, -2px -2px 4px #ffffff}.todo-item-body{display:flex;flex-direction:row;justify-content:space-between;align-items:center;flex-grow:1}@-webkit-keyframes raiseTodo{0%{box-shadow:none}100%{box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5)}}@keyframes raiseTodo{0%{box-shadow:none}100%{box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5)}}

@-webkit-keyframes addGrow{0%{width:50px;height:50px;border-radius:50%}25%{height:75px;border-radius:15px}100%{width:100%}}@keyframes addGrow{0%{width:50px;height:50px;border-radius:50%}25%{height:75px;border-radius:15px}100%{width:100%}}@-webkit-keyframes addShrink{0%{width:100%;height:75px;border-radius:15px}25%{height:50px}90%{border-radius:30px}100%{width:50px;border-radius:50%}}@keyframes addShrink{0%{width:100%;height:75px;border-radius:15px}25%{height:50px}90%{border-radius:30px}100%{width:50px;border-radius:50%}}.add-button{display:flex;flex-direction:row;justify-content:space-between;align-items:center;width:100%;height:75px;padding:0 15px;font-size:36px;font-weight:700;color:rgba(67,75,95,0.7);background:transparent;border:none;border-radius:15px;box-shadow:9px 9px 16px rgba(163,177,198,0.6),-9px -9px 16px rgba(255,255,255,0.5)}.add-button.closed{padding:0;border-radius:50%;background:linear-gradient(145deg, #f0f5fd, #caced4);box-shadow:5px 5px 10px #bec3c9, -4px -4px 12px #ffffff}.add-button.closed>button{background:transparent;padding:0;margin:0;height:50px;width:50px;font-size:36px;font-weight:700;text-align:center;color:rgba(67,75,95,0.7);box-shadow:none;border:none;border-radius:50%;outline:none;cursor:pointer}.add-button.closed:active{border-radius:50%;background:linear-gradient(145deg, #caced4, #f0f5fd);text-shadow:5px 5px 10px #bec3c9, -5px -5px 10px #ffffff}.add-button>.add-form{display:flex;flex-grow:1;margin-right:20px}.add-button>.add-form.closed{display:none;width:0;height:0}.add-button>button{background:transparent;padding:0;margin-right:10px;height:50px;width:50px;font-size:36px;font-weight:700;text-align:center;border:none;border-radius:50%;outline:none;cursor:pointer;color:rgba(67,75,95,0.7);background:linear-gradient(145deg, #f0f5fd, #caced4);box-shadow:5px 5px 10px #bec3c9, -4px -4px 12px #ffffff}.add-button>button:active{background:linear-gradient(145deg, #caced4, #f0f5fd);text-shadow:5px 5px 10px #bec3c9, -5px -5px 10px #ffffff}input[type='text'].todo-edit{width:100%;height:50px;padding:0 10px;border-radius:15px;box-shadow:5px 5px 15px #bec3c9 inset, -5px -5px 15px #ffffff inset;border:none;background:transparent;margin:auto;outline:none;color:rgba(67,75,95,0.7);font-size:16px;padding-left:10px;font-family:'Open Sans';caret-color:rgba(67,75,95,0.7)}

